import React, { useEffect, useState, FC } from "react";

import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";

import { getManifest } from "api/missionManifests";
import Manifest, { getEmptyManifest } from "entities/Manifest";
import RoverSelector from "components/custom/RoverSelector/roverSelector";
import RoversName from "src/entities/RoversName";

const GridItem: FC<{ text: string; value: string }> = ({ text, value }) => {
  return (
    <>
      <Grid item xs={8}>
        <Typography variant='h5' align='left' color='text.secondary' paragraph>
          {text}
        </Typography>
      </Grid>
      <Grid item xs={4}>
        <Typography variant='h5' align='left' color='text.secondary' paragraph>
          {value}
        </Typography>
      </Grid>
    </>
  );
};

const ManifestInfo: FC<{ roverManifest: Manifest }> = ({ roverManifest: roverName }) => {
  const [manifest, setManifest] = useState<Manifest>(getEmptyManifest());

  useEffect(() => {
    getManifest(roverName).then((apiResp) => {
      setManifest(apiResp);
    });
  }, [roverName]);

  return (
    <Grid container columnSpacing={1} rowSpacing={1}>
      <GridItem text="The Rover's launch date from Earth:" value={manifest.launch_date} />
      <GridItem text="The Rover's landing date on Mars:" value={manifest.landing_date} />
      <GridItem text="The Rover's mission status:" value={manifest.status} />
      <GridItem
        text='The most recent Martian sol from which photos exist:'
        value={manifest.max_sol.toString()}
      />
      <GridItem
        text='The most recent Earth date from which photos exist:'
        value={manifest.max_date}
      />
      <GridItem
        text='Number of photos taken by that Rover:'
        value={manifest.photos.length.toString()}
      />
    </Grid>
  );
};

export default ManifestInfo;
